<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/teacher" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/questionBank" tag="span">题库管理</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/examPaper" tag="span">试卷管理</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examSchedule" tag="span">考试安排</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examGrading" tag="span">阅卷</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/class" tag="span">班级管理</router-link></el-menu-item>
                <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>
            </el-menu>
        </el-footer>


        <el-main>

            <el-form :model="inputData" :inline="true" label-width="50px" style="width: 500px;"
                class="demo-form-inline">
                <el-form-item prop="key">
                    <el-input v-model="inputData" placeholder="请输入关键字以搜索"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="likeGet">查询</el-button>
                </el-form-item>
                <!-- <el-form-item>
                    <el-button type="primary" @click="dialogFormVisible2 = true">添加</el-button>
                </el-form-item> -->
            </el-form>


            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="schemeNum" label="考试编号" width="200">
                </el-table-column>
                <el-table-column prop="schemeBegin" label="开始时间" width="200">
                </el-table-column>
                <el-table-column prop="schemeEnd" label="结束时间" width="200">
                </el-table-column>
                <el-table-column prop="examNum" label="试卷编号" width="200">
                </el-table-column>
                <el-table-column prop="course.courseName" label="考试课程" width="200">
                </el-table-column>
                <el-table-column prop="examType.typeName" label="考试类型" width="200">
                </el-table-column>
                <el-table-column label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button type="text" @click="dialogFormVisible = true">修改</el-button>
                        <el-dialog title="修改" :visible.sync="dialogFormVisible">
                            <el-form :model="form">
                                <el-form-item label="开始时间" :label-width="formLabelWidth">
                                    <el-input v-model="form.schemeBegin" autocomplete="off"></el-input>
                                </el-form-item>

                                <el-form-item label="结束时间" :label-width="formLabelWidth">
                                    <el-input v-model="form.schemeEnd" autocomplete="off"></el-input>
                                </el-form-item>

                                <el-form-item label="试卷编号" :label-width="formLabelWidth">
                                    <el-input v-model="form.examNum" autocomplete="off"></el-input>
                                </el-form-item>
                                <!-- <el-form-item label="考试课程" :label-width="formLabelWidth">
                                    <el-input v-model="form.course.courseName" autocomplete="off"></el-input>
                                </el-form-item> -->
                                <el-form-item label="考试类型" :label-width="formLabelWidth">
                                    <el-input v-model="form.examType.typeName" autocomplete="off"></el-input>
                                </el-form-item>
                                <!-- <el-form-item label="参与班级" :label-width="formLabelWidth">
                                    <el-input v-model="form.classNum" autocomplete="off"></el-input>
                                </el-form-item> -->
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="reset">取 消</el-button>
                                <el-button type="primary" @click="formUpdate(scope.row.schemeNum)">确 定</el-button>
                            </div>
                        </el-dialog>
                        <el-button @click="deleteById(scope.row.schemeNum)" type="text" size="medium">删除</el-button>
                        <!-- <el-button type="text" @click="getClass(scope.row.schemeNum)">查看</el-button>
                        <el-dialog title="班级详情" :visible.sync="dialogTableVisible">
                            <el-table :data="classData">
                                <el-table-column property="classID" label="班级编号" width="150"></el-table-column>
                                <el-table-column property="className" label="班级名称" width="200"></el-table-column>
                            </el-table>
                        </el-dialog> -->
                    </template>
                </el-table-column>
            </el-table>
            <!-- 对话框，移出 el-form-item -->
            <el-dialog title="添加" :visible.sync="dialogFormVisible2">
                <el-form :model="form2">
                    <el-form-item label="开始时间" :label-width="formLabelWidth">
                        <el-input v-model="form2.schemeBegin" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item label="结束时间" :label-width="formLabelWidth">
                        <el-input v-model="form2.schemeEnd" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="试卷编号" :label-width="formLabelWidth">
                        <el-input v-model="form2.examNum" autocomplete="off"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="考试课程" :label-width="formLabelWidth">
                        <el-input v-model="form2.course.courseName" autocomplete="off"></el-input>
                    </el-form-item> -->
                    <!-- <el-form-item label="考试科目" :label-width="formLabelWidth">
                    <el-select v-model="form2.course.courseName" placeholder="请选择考试类型">
                        <el-option label="JAVA" value="JAVA"></el-option>
                        <el-option label="C++" value="C++"></el-option>
                        <el-option label="流体力学" value="流体力学"></el-option>
                        <el-option label="土木工程材料" value="土木工程材料"></el-option>
                    </el-select>
                </el-form-item> -->
                    <el-form-item label="考试类型" :label-width="formLabelWidth">
                    <el-select v-model="form2.examType.typeName" placeholder="请选择考试类型">
                        <el-option label="期中考试" value="期中考试"></el-option>
                        <el-option label="期末考核" value="期末考试"></el-option>
                    </el-select>
                </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="reset">取 消</el-button>
                    <el-button type="primary" @click="insertExam">确 定</el-button>
                </div>
            </el-dialog>
        </el-main>
    </el-container>

</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            tableData: [],
            classData: [],
            inputData: '',
            dialogTableVisible: false,
            dialogFormVisible: false,
            dialogFormVisible2: false,
            formLabelWidth: '120px',
            //绑定修改数据
            form: {
                schemeNum: '',
                schemeBegin: '',
                schemeEnd: '',
                classNum: '',
                teacherNum: '',
                examNum: '',
                examType: {
                    typeNum: '',
                    typeName: '',
                }
            },
            //绑定添加数据
            // form2: {
            //     schemeNum: '',
            //     schemeBegin: '',
            //     schemeEnd: '',
            //     classNum: '',
            //     teacherNum: '',
            //     examNum: '',
            //     course: {
            //         courseName: "",
            //         courseNum: null,
            //         courseCredit: null,
            //         coursePreiod: null,
            //         semester: "",
            //         reviewType: {
            //             typeNum: null,
            //             typeName: ""
            //         },
            //     },
            //     examType: {
            //         typeNum: '',
            //         typeName: '',
            //     }
            // },
            form2: {
                schemeNum: '',
                schemeBegin: '',
                schemeEnd: '',
                classNum: '',
                teacherNum: '',
                examNum: '',
                examType: {
                    typeNum: '',
                    typeName: '',
                }
            },



        }
    },
    methods: {
        handleClick(row) {
            console.log(row);
        },
        onSubmit() {
            console.log('submit!');
        },
        //根据考试编号查看考试班级
        getClass(scheduleID) {
            axios.get(`/api/examScheme/${scheduleID}`).then((result) => {
                this.dialogTableVisible = true
                this.classData = result.data.data;
            });

        },
        //查询全部
        getAll() {
            axios.get("/api/examScheme").then((result) => {
                this.tableData = result.data.data;
            });
        },
        //模糊查询
        likeGet() {
            axios.get(`/api/examScheme/inputData/${this.inputData}`).then((result) => {
                this.tableData = result.data.data;
            });

        },
        //修改表单
        formUpdate(id) {
            this.form.schemeNum=id;
            axios.post("/api/examScheme", this.form).then((result) => {
                if (result.data.code == 1) {
                    this.dialogFormVisible = false;
                    this.getAll();
                    this.open2();

                }
                else {
                    this.open3();

                }
            });

        },
        //删除数据
        deleteById(classID) {
            axios.delete(`/api/examScheme/${classID}`).then((result) => {
                if (result.data.code == 1) {
                    this.getAll();
                    this.open2();
                }
                else {
                    this.open3();
                }


            });

        },
        //添加试卷
        insertExam() {
            if(this.form2.examType.typeName=="期末考试"){
                this.form2.examType.typeNum==1;
            }

            // if(this.form2.course.courseName=="JAVA"){
            //     this.form2.course.courseNum==1;
            //     this.form2.course.courseCredit==4.0;
            //     this.form2.course.coursePreiod==72;
            //     this.form2.course.semester=="大一下";
            //     this.form2.course.reviewType.typeNum==1;
            //     this.form2.course.reviewType.typeName=="考试";
            // }
            axios.post("/api/examScheme", this.form2).then((result) => {
                if (result.data.code == 1) {
                    this.getAll();
                    this.open2();

                }
                else {
                    this.open3();
                }

            });

        },
        //重置
        reset() {
            this.form = '';
            this.dialogFormVisible = false
        },
        //成功提示
        open2() {
            this.$message({
                message: '恭喜你，这是一条成功消息',
                type: 'success'
            });
        },
        //错误提示
        open3() {
            this.$message({
                message: '警告哦，这是一条警告消息',
                type: 'warning'
            });
        },


    },
    mounted() {
        this.getAll();
    }
}
</script>

<style scoped>
.el-container {
    min-height: 100vh;
    /* 至少占据整个视口高度 */
    max-height: 1200px;
    /* 最大高度限制 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
}

.el-header,
.el-footer {
    flex: 0 0 auto;
    /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
}

.el-main {
    flex: 1 1 auto;
    /* 自动占据剩余空间 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
}
</style>